// cross stylings 

.close{
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 30px;
  height: 30px;
  margin-bottom: 14px;
  overflow: hidden;
  &:hover {
    &::before, &::after {
      background: grey;
    }
  }

  &::before, &::after {
    content: '';
    position: absolute;
    height: 3px;
    width: 100%;
    top: 50%;
    left: 0;
    margin-top: -1px;
    background: white;
  }
  &::before {
  	transform: rotate(45deg);
    // @include transform(rotate(45deg));
  }
  &::after {
  	transform: rotate(-45deg);
    // @include transform(rotate(-45deg));
  }
  &.thick {
    &::before, &::after {
      height: 3px;
      margin-top: -2px;
    }
  }
}

