//menu styling
$bar: 7px;
$foo: $bar * 11;


.menuToggle {
  width: $foo;
}

.loading-bar {
  width: $foo;
  height: $bar;
  padding-top:10px;
  
  .loading {
    position: relative;
    border-radius:10px;
    width: $bar;
    height: $bar;
    background: #2a36b1;
    animation: grow 3s linear infinite, move 3s linear infinite;
    
    &:after {
      display: block;
      content: '';
      position: relative;
      width: $bar;
      height: $bar / 2;
      padding: 0 5px;
      top: $bar * 2;
      left: -5px;
      background: radial-gradient(ellipse at center, rgba(0,0,0,0.15) 0%,rgba(0,0,0,0) 100%);
      animation: grow 3s linear infinite;
    }
  }
}

@keyframes move {
  0%    { left: 0           ; }
  16.7% { left: 0           ; }
  33.3% { left: $foo / 2    ; }
  50%   { left: $foo - $bar ; }
  66.7% { left: $foo / 2    ; }
  83.3% { left: 0           ; }
  100%  { left: 0           ; }
}

@keyframes grow {
  0%    { width: $bar     ; }
  16.7% { width: $foo / 2 ; }
  33.3% { width: $foo / 2 ; }
  50%   { width: $bar     ; }
  66.7% { width: $foo / 2 ; }
  83.3% { width: $foo / 2 ; }
  100%  { width: $bar     ; }
}
